import { ResourceStatusCode } from '../enums/ResourceStatusCode';
import { AccountRoleCode } from '../enums/AccountRoleCode';
import { ResourceClassCode } from '../enums/ResourceClassCode';
import { ResourceTypeMask } from '../enums/ResourceTypeMask';

/* 
*/
export class AdminResourceExportCsvRequest {
 
    // Free text search on resource Id, Brand or description 
    public search: string;
 
    // Filter by Resource class (UNDEFINED means no filter) 
    public resourceClass: ResourceClassCode;
 
    // Filter by Resource type (UNDEFINED means no filter) 
    public resourceType: ResourceTypeMask;
 
    // Filter by Resource status (UNDEFINED means no filter) 
    public status: ResourceStatusCode;
 
    // Filter by usage (UNDEFINED means no filter) 
    public forUseBy: AccountRoleCode;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    constructor(search?: string, resourceClass?: ResourceClassCode, resourceType?: ResourceTypeMask, status?: ResourceStatusCode, forUseBy?: AccountRoleCode, sort?: string) { 
        this.search = search;
        this.resourceClass = resourceClass;
        this.resourceType = resourceType;
        this.status = status;
        this.forUseBy = forUseBy;
        this.sort = sort;
    }

}


/*
 *  Interface of AdminResourceExportCsvRequest
*/
export interface IAdminResourceExportCsvRequest {
 
    // Free text search on resource Id, Brand or description 
    search?: string;
 
    // Filter by Resource class (UNDEFINED means no filter) 
    resourceClass?: ResourceClassCode;
 
    // Filter by Resource type (UNDEFINED means no filter) 
    resourceType?: ResourceTypeMask;
 
    // Filter by Resource status (UNDEFINED means no filter) 
    status?: ResourceStatusCode;
 
    // Filter by usage (UNDEFINED means no filter) 
    forUseBy?: AccountRoleCode;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    sort?: string;
 
}
